.Header {
  margin-left: 23%;
  margin-top: 100px;
}
.button {
  padding: 16px 24px;
  background: #000;
  color: #fff;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  transition: .3s;
  outline: none;
  margin: 15px 0;
}
.button:hover {
  background: #222;
}
.opacity {
  animation: fadeInFromNone 0.5s ease-out;
}

td {
  padding: 0 30px;
}

@keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}